@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/PTSans-Regular.woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/PTSans-Bold.woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
*:focus {
  outline: none;
}
html,
body {
  margin: 0;
  padding: 0;
}
html {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
body {
  width: 100vw;
  max-width: 1600px;
  margin: 0 auto;
  background-color: white;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
  overflow-x: hidden;
  position: relative;
}
main {
  grid-area: main;
  height: 100%;
}
a {
  text-decoration: none;
}
img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}
textarea {
  resize: none;
}
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
  display: block;
}
.standard-button {
  display: inline-flex;
  justify-content: center;
  padding: 15px 30px 15px 30px;
  background-color: #81b3d2;
  color: #ffffff;
  color: #FFF;
  font-family: PT Sans;
  font-size: 21px;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}
.standard-button:hover,
.standard-button:focus {
  background-color: #669EC0;
  outline: none;
}
.standard-button:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
.standard-button:disabled {
  background-color: #E5E5E5;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .standard-button {
    padding: 17px 30px 17px 30px;
  }
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  margin: 0 auto;
}
.layout-wrapper {
  padding: 0 17px 0 17px;
}
@media (min-width: 768px) {
  .layout-wrapper {
    padding: 0 30px 0 30px;
  }
}
@media (min-width: 1200px) {
  .layout-wrapper {
    padding: 0 60px 0 60px;
  }
}
.modal-opened {
  overflow: hidden;
}
.header {
  position: relative;
  width: 100%;
  max-width: 1600px;
}
.header__nav {
  margin: 0;
  padding: 0;
  padding-top: 55px;
}
@media (min-width: 768px) {
  .header__nav {
    padding-top: 17px;
  }
}
.header__logo {
  position: absolute;
  left: calc(50% - 90px);
  z-index: 5;
}
.header__open-button {
  display: block;
  position: absolute;
  top: 17px;
  right: 17px;
  width: 22px;
  height: 22px;
  border: none;
  background-color: transparent;
  background-image: url("../../images/SVG/icon-menu.svg.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  z-index: 15;
  cursor: pointer;
}
.header__open-button:hover,
.header__open-button:focus {
  opacity: 0.7;
}
.header__open-button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header__open-button {
    display: none;
  }
}
.header__close-button {
  display: block;
  position: absolute;
  z-index: 15;
  width: 22px;
  height: 22px;
  top: 17px;
  right: 17px;
  border: none;
  background-color: transparent;
  background-image: url("../../images/icon-cross.png");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  cursor: pointer;
}
.header__close-button:hover,
.header__close-button:focus {
  opacity: 0.7;
}
.header__close-button:active {
  opacity: 0.3;
}
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__picture-block {
  width: 100%;
  height: auto;
  min-height: 389px;
  background-color: #81b3d2;
  position: relative;
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .hero__picture-block {
    min-height: 518px;
  }
}
.hero__picture-block::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  z-index: 5;
  background-image: url("../images/text-index.png");
  background-repeat: no-repeat;
  background-size: 290px 212px;
  background-position: center;
}
@media (min-width: 768px) {
  .hero__picture-block::before {
    background-size: 460px 351px;
  }
}
.hero__picture-block::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  z-index: 3;
  background-image: url("../images/intro-triangle.png");
  background-repeat: no-repeat;
  background-position: 50% bottom;
  background-size: 178%;
  top: 1px;
  left: 0;
}
@media (min-width: 768px) {
  .hero__picture-block::after {
    background-size: 158%;
  }
}
@media (min-width: 1200px) {
  .hero__picture-block::after {
    background-size: 100%;
  }
}
.hero__picture-block img {
  object-fit: cover;
  height: 100%;
  min-height: 389px;
}
@media (min-width: 768px) {
  .hero__picture-block img {
    min-height: 518px;
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .hero__picture-block img {
    width: 100%;
  }
}
.hero__text-block {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .hero__text-block {
