.main-menu {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  z-index: 10;
  width: 100%;
  left: 0;
  top: 0;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-menu {
    grid-template-columns: auto auto auto auto;
    background-color: #ffffff;
    position: static;
    margin-bottom: 17px;
    grid-column-gap: 30px;
    grid-row-gap: 15px;
  }
}
@media (min-width: 1200px) {
  .main-menu {
    grid-column-gap: 80px;
  }
}
.hidden {
  display: none;
}
