.hurry {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
}
@media (min-width: 768px) {
  .hurry {
    margin-bottom: 72px;
  }
}
@media (min-width: 1200px) {
  .hurry {
    margin-bottom: 90px;
  }
}
.hurry__text {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4px;
}
.hurry h2 {
  color: #000;
  text-align: center;
  font-family: PT Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .hurry h2 {
    font-size: 30px;
    margin-bottom: 20px;
  }
}
.hurry p {
  color: #333;
  font-family: PT Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .hurry p {
    width: 64%;
    margin-bottom: 90px;
  }
}
@media (min-width: 1200px) {
  .hurry p {
    width: 50%;
  }
}
.hurry__image {
  width: 100%;
  height: auto;
  min-height: 177px;
  position: relative;
}
@media (min-width: 768px) {
  .hurry__image {
    height: 500px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1200px) {
  .hurry__image {
    width: 800px;
    padding-left: 0;
    padding-right: 0;
  }
}
.hurry__image::after {
  width: 48px;
  height: 54px;
  position: absolute;
  content: "";
  cursor: pointer;
  background-image: url("../../images/Polygon.png");
  background-repeat: no-repeat;
  top: calc(50% - 24px);
  left: calc(50% - 27px);
  background-position: center;
  background-size: 100%;
}
.hurry__image img {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
