.catalog {
  padding: 0 17px 0 17px;
}
@media (min-width: 768px) {
  .catalog {
    padding: 0 60px 0 60px;
  }
}
@media (min-width: 1200px) {
  .catalog {
    padding: 0;
  }
}
.catalog__list {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto;
  grid-auto-flow: row;
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .catalog__list {
    grid-auto-flow: dense;
    grid-template-columns: 50% 50%;
    margin-bottom: 40px;
  }
}
@media (min-width: 1200px) {
  .catalog__list {
    grid-template-columns: auto auto auto;
    margin-bottom: 54px;
  }
}
.catalog__list li:first-child {
  position: relative;
}
@media (min-width: 768px) {
  .catalog__list li:first-child {
    grid-column: 1 / 3;
  }
}
@media (min-width: 1200px) {
  .catalog__list li:first-child {
    grid-column: 1 / 4;
  }
}
.catalog__list li:first-child::before {
  position: absolute;
  content: "";
  background-color: #E5E5E5;
  height: 1px;
  width: 100%;
  top: -18px;
  left: 0;
}
@media (min-width: 768px) {
  .catalog__list li:first-child::before {
    display: none;
  }
}
.catalog__list li:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .catalog__list li:first-child img {
    max-height: 400px;
  }
}
.catalog__list li img:not(.catalog__list li:first-child img) {
  max-height: 268px;
}
@media (min-width: 768px) {
  .catalog__list li:nth-child(3n + 2) .card__description {
    background: #EAEAEA;
  }
}
@media (min-width: 1200px) {
  .catalog__list li:nth-child(3n + 2) .card__description {
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.modal {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  position: absolute;
  z-index: 10;
}
.modal-closed {
  display: none;
}
.modal__wrapper {
  position: relative;
}
.error-window {
  position: fixed;
  width: 100%;
  height: auto;
  background-color: #ffffff;
  top: 20%;
  padding: 40px 40px 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 16px 18px -5px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .error-window {
    width: 400px;
    left: calc(50% - 200px);
  }
}
.success-window {
  position: fixed;
  width: 100%;
  height: auto;
  background-color: #ffffff;
  top: 40%;
  padding: 40px 40px 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 16px 18px -5px rgba(0, 0, 0, 0.75);
}
@media (min-width: 768px) {
  .success-window {
    width: 640px;
    left: calc(50% - 320px);
    top: 15%;
  }
}
.error-window h2,
.success-window h2 {
  color: #000000;
  text-align: center;
  font-family: PT Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.error-window p,
.success-window p {
  color: #333;
  font-family: PT Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 30px;
}
.ok-button {
  width: 85%;
  margin-bottom: 40px;
}
.closewindow-button {
  width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .closewindow-button {
    margin-bottom: 35px;
    width: 90%;
  }
}
